.PanelHeader__in {
    background-color: rgb(#43225e) !important;
    background: -webkit-linear-gradient(50deg, #131111,#43225e)!important;
    background: linear-gradient(50deg, #131111,#43225e)!important;
    border-bottom: 1PX solid #131111;
  }

  .Panel__in {
    background: rgba(0, 0, 0, 0)!important;
  }

  .Panel::after {
    position: fixed!important;
    background: -webkit-linear-gradient(70deg, #131111,#43225e)!important;
    background: linear-gradient(70deg, #131111,#43225e)!important;
    background-color: black!important;
    background-position: center!important;
    background-repeat: no-repeat!important;
    background-size: 100%!important;
    filter: blur(3px);
    filter: brightness(60%);
  }

.vkuiFormField__border {
  background-color: rgb(27, 6, 122) !important;
  background: -webkit-linear-gradient(50deg, #131111,#43225e)!important;
  background: linear-gradient(50deg, #131111,#43225e)!important;
  opacity: 0.9;
  }

  .vkuiCustomSelect__options {
    background-color: rgb(27, 6, 122) !important;
    background: -webkit-linear-gradient(90deg, #131111,#43225e)!important;
    background: linear-gradient(90deg, #131111,#43225e)!important;
    opacity: 0.9;
  }